import React from 'react';
import { useState } from 'react';
import { Table } from 'react-bootstrap';
import { connect } from 'react-redux';
import { useHistory, useParams } from 'react-router';
import { useEffect } from "react";


function Cart(props){

    let history = useHistory();

    let {id} = useParams();
    let 선택매뉴 = props.menu.find(function(상품){
        return 상품.id == id;
    })
    let [ 합계, 합계변경 ] = useState(선택매뉴.price * 선택매뉴.count);

    useEffect(()=>{
        let plus = 0;

        for(let i = 0; i < props.state.length; i++){
            
            plus = plus + (props.state[i].price * props.state[i].count)
        }
        console.log(plus);
        합계변경(합계 + plus);
    },[]);

    return(
        <div className="cartTable container">
            <Table responsive="sm">
                <thead>
                <tr>
                    <th>#</th>
                    <th> 상품명 </th>
                    <th> 수량 </th>
                    <th> 가격 </th>
                </tr>
                </thead>
                <tbody>
                        <tr>
                            <td>0</td>
                            <td>{ 선택매뉴.title }</td>
                            <td>{ 선택매뉴.count }</td>
                            <td>{ 선택매뉴.price * 선택매뉴.count } 원</td>
                        </tr>
                {
                    props.state.map((a, i)=>{
                       return(
                        <tr key={i}>
                            <td>{ a.id + 1}</td>
                            <td>{ a.title }</td>
                            <td>{ a.count }</td>
                            <td>{ a.count * a.price } 원</td>
                        </tr>
                       ) 
                    })
                }
                    <tr>
                        <td></td>
                        <td></td>
                        <td></td>
                        <td>총 합계: { 합계 } 원</td>
                    </tr>
                </tbody>
            </Table>
            <div className="btnBox">
                <button class="btn btn-success" onClick={()=>{
                    history.push('/');
                    props.dispatch({type: '복구'})
                }}> 결제완료 </button>&nbsp;
                <button class="btn btn-secondary" onClick={()=>{
                    history.goBack();
                }}> 이전으로 </button>
            </div>
            
        </div>
    )
    
}

function asd(state){
    return {
        state : state
    }
}

export default connect(asd)(Cart)
