import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import { HashRouter } from 'react-router-dom';
import { Provider } from 'react-redux';
import { createStore } from 'redux';

let 기본state = [
                  {
                      id: 0,
                      title: "떡추가",
                      price: 1000,
                      count: 0,
                      text: "https://www.yupdduk.com/images/menu/2020_img_dduk.png"
                  },
                  {
                      id: 1,
                      title: "오뎅추가",
                      price: 1000,
                      count: 0,
                      text: "https://www.yupdduk.com/images/menu/2020_img_fishcake.png"
                  },
                  {
                      id: 2,
                      title: "계란추가",
                      price: 1500,
                      count: 0,
                      text: "https://www.yupdduk.com/images/menu/2020_img_030240.png"
                  }
                ]

function reducer(state = 기본state, 액션){

  if( 액션.type === '증가0' ){
      let copy = [...state];
      copy[0].count = 액션.payload;
      return copy;
  } else if (액션.type === '증가1'){
      let copy = [...state];
      copy[1].count = 액션.payload;
      return copy;
  } else if (액션.type === '증가2'){
      let copy = [...state];
      copy[2].count = 액션.payload;
      return copy;
  } else if (액션.type === "복구"){  
      let copy = [...state];
      copy[0].count = 0;
      copy[1].count = 0;
      copy[2].count = 0;
      return copy;
  } else {
      return state;
  }
  
  
}

let store = createStore(reducer);

ReactDOM.render(
  <React.StrictMode>
    <HashRouter>
      <Provider store={store}>
        <App />
      </Provider>
    </HashRouter>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
