import React from 'react';
import { useState } from "react";
import { Navbar, Nav, NavDropdown, Form, FormControl, Button, Jumbotron } from 'react-bootstrap';
import { connect } from 'react-redux';

function SubMenu(props){
    
    let 증가함수 = "";

    return(
        <div className="col-md-4">
            <img src={props.sub.text} width="100%"/>
            <h4> {props.sub.title} </h4>
            <p> {props.sub.price} 원</p>
            <Form>
                <Form.Group controlId="exampleForm.SelectCustom">
                    <Form.Control as="select" custom onChange={(e)=>{
                        console.log(props.sub.title)
                        console.log(e.target.value)

                        증가함수 = "증가" + props.sub.id;

                        console.log(증가함수)

                        props.dispatch({type: 증가함수, payload: e.target.value})
                    }} >
                        <option>0</option>
                        <option>1</option>
                        <option>2</option>
                        <option>3</option>
                    </Form.Control>
                </Form.Group>
            </Form>
        </div>
    )
}

function asd(state){
    return {
        state : state
    }
}

export default connect(asd)(SubMenu)